body {
	color: $dark;
	min-width: 320px;
	font-family: $graphik;
	@include f(18px,400,1.6);
	background: $white;
	@include font-smoothing;
	&.is-fixed {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0; } }
.row {
	margin: 0 -20px;
	@include clr; }
.col-33 {
	float: left;
	width: 33.3%;
	padding: 0 20px;
	&-md {
		@include md {
			width: 100%; } } }
