.info {
	// padding: 36px 0 80px
	padding: 303px 0 80px;
	@include lg {
		padding-top: 157px; }
	@include md {
		padding: 40px 0 100px; }
	@include sm {
		padding-bottom: 63px;
		margin-bottom: 100px;
		border-bottom: 1px solid #D4D4D4; }
	&__label {
		margin-bottom: 25px;
		@include sm {
			margin-bottom: 26px; } }
	&__title {
		padding-bottom: 85px;
		border-bottom: 1px solid #D4D4D4;
		@include f(45px, 400, 59px);
		@include lg {
			padding-bottom: 60px;
			@include f(36px, null, 1.3); }
		@include sm {
			padding-bottom: 43px;
			@include f(22px, null, 31px); }
		strong {
			font-weight: 800; } }
	&__subtitle {
		padding: 59px 0 62px;
		max-width: 925px;
		@include f(28px, 400, 41px);
		@include lg {
			padding: 40px 0;
			@include f(26px, null, 1.3); }
		@include sm {
			padding: 39px 0 12px;
			@include f(18px, null, 26px); } }
	&__items {
		margin: 0 -20px 30px;
		font-size: 0;
		@include lg {
			margin: 0 -10px 30px; }
		@include sm {
			margin: 0; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 20px;
		width: 33.33%;
		@include lg {
			padding: 0 10px;
 }			// width: 50%
		@include sm {
			padding: 0;
			width: 100%; }
		// &:nth-child(3)
		// 	+lg
 }		// 		width: 100%
	&__additional {
		margin-top: -14px; } }
