.topic {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	color: $white;
	background-image: linear-gradient(151deg, #0A2F2A 8%, #2D5442 100%);
	&_in {
		position: relative;
		min-height: 515px;
		height: auto;
		@include sm {
			min-height: 330px; } }
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 1;
		background-position: 38% 26%;
		background-repeat: no-repeat;
		@include xl {
			background-position: 50% 26%; }
		.topic_in & {
			background-position: 39% 37%; }
		@include sm {
			display: none; }
		&[data-anim-from="right-20"] {
			position: absolute; }
		&_mob {
			display: none;
			background-position: -50% 20%;
			background-size: 142% auto;
			@include sm {
				display: block; } } }
	&__left {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		z-index: 2;
		padding: 260px 0 44px 40px;
		font-size: 12px;
		&[data-anim-from] {
			position: absolute; }
		@include md {
			display: none; } }
	&__mail {
		position: absolute;
		left: 73px;
		bottom: 33px;
		transform: rotate(180deg);
		@include xl {
			left: 40px; } }
	&__inner {
		position: relative;
		z-index: 1;
		padding: 170px 40px 135px 419px;
		max-width: 1680px;
		height: 100vh;
		min-height: 530px;
		transition: height .3s;
		text-align: left;
		@include vertical;
		&.is-fixed {
			position: fixed;
			top: 0;
			left: 0;
			right: 0; }
		.topic_in & {
			padding: 110px 40px 75px 419px;
			min-height: 0;
			@include xl {
				padding: 150px 20px 130px 120px; }
			@include md {
				padding: 150px 20px 130px; } }
		@include xl {
			padding-left: 120px;
			text-align: center; }
		@include md {
			padding: 80px 20px 140px; } }
	&__wrap {
		position: relative;
		width: 100%;
		z-index: 3;
		text-align: left;
		vertical-align: middle;
		display: inline-block;
		@include md {
			max-width: 778px; }
		@include sm {
			max-width: 510px; } }
	&__footer {
		margin-top: 35px;
		.btn-transparent {
			@include sm {
				display: none; } } }
	&__tooltip {
		position: absolute;
		right: 19px;
		bottom: 26px;
		opacity: 0;
		@include md {
			bottom: 80px; } } }

.mail {
	color: $white;
	font-size: 12px;
	font-weight: 400;
	writing-mode: vertical-lr;
	&:before {
		position: absolute;
		content: "";
		top: -5px;
		left: -5px;
		right: -5px;
		bottom: -5px; } }

.share {
	font-size: 15px;
	&__item {
		display: block;
		&:not(:last-child) {
			margin-bottom: 5px; } }
	a {
		position: relative;
		color: inherit;
		display: block;
		&:before {
			position: absolute;
			content: "";
			top: -4px;
			left: -5px;
			right: -5px;
			bottom: -4px; } } }
