.footer {
	position: relative;
	z-index: 101;
	width: 100%;
	overflow: hidden;
	padding-top: 90px;
	color: $white;
	background: $blue;
	&_home {
		padding-top: 282px;
		@include xl {
			padding-top: 120px; }
		@include sm {
			padding-top: 29px; }
		.footer {
			&__container {
				padding: 40px 40px 48px;
				margin: 0 auto;
				max-width: 1680px;
				@include xl {
					padding-left: 20px;
					padding-right: 20px; }
				@include sm {
					padding-bottom: 20px; } }
			&__inner {
				position: relative;
				margin-left: 380px;
				@include xl {
					margin-left: 340px; }
				@include md {
					margin-left: 0; } } } }
	&_default {
		@include sm {
			padding-top: 45px; }
		.footer {
			&__container {
				padding-bottom: 20px; }
			&__top {
				min-height: 451px;
				@include lg {
					min-height: 1px; } }
			&__title {
				margin-bottom: 56px;
				@include sm {
					margin-bottom: 37px; } }
			&__btn {
				margin-bottom: 50px;
				@include sm {
					margin-bottom: 90px; } }
			&__inner {
				padding: 0 60px 0 20px;
				max-width: 880px;
				@include sm {
					padding: 0 20px; } } } }
	&_md {
		padding-top: 78px;
		@include sm {
			padding-top: 45px; }
		.footer {
			&__container {
				padding-bottom: 50px;
				@include sm {
					padding-bottom: 20px; } }
			&__title {
				margin-right: -151px;
				margin-bottom: 43px;
				@include lg {
					margin-right: 0; }
				@include sm {
					margin-bottom: 29px; } }
			&__btn {
				@include sm {
					margin-bottom: 121px; } }
			&__top {
				min-height: 599px;
				@include lg {
					min-height: 1px; } }
			&__inner {
				padding: 0 60px 0 20px;
				max-width: 880px;
				@include sm {
					padding: 0 20px; } } } }
	&__inner {
		margin: 0 auto;
		max-width: 800px; }
	&__label {
		margin-bottom: 34px;
		color: inherit;
		@include sm {
			margin-bottom: 15px; } }
	&__title {
		margin-right: -50%;
		margin-bottom: 63px;
		@include xl {
			margin-right: 0; }
		@include sm {
			margin-bottom: 37px; } }
	&__btn {
		margin-bottom: 163px;
		@include sm {
			margin-bottom: 139px; } }
	&__description {
		padding-bottom: 19px;
		@include sm {
			margin-right: -9px; }
		p {
			margin-bottom: 34px;
			@include f(24px, null, 34px);
			@include sm {
				margin-bottom: 28px;
				@include f(14px, null, 21px); } } }
	&__logo {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 14px;
		color: inherit;
		font-size: 0;
		@include sm {
			margin-bottom: 19px; }
		span {
			display: block;
			padding-bottom: 3px;
			@include f(9px, 500, 11px);
			text-transform: uppercase; }
		img {
			height: 26px; } }
	&__info {
		@include f(12px, null, 18px); }
	&__bottom {
		padding: 16px 40px;
		border-top: 1px solid rgba($white, 0.2);
		font-size: 0;
		@include clr;
		@include lg {
			padding-left: 20px;
			padding-right: 20px; }
		li {
			display: inline-block;
			vertical-align: bottom;
			@include f(12px, null, 14px);
			@include sm {
				@include f(9px, null, 2); }
			&:not(:last-child) {
				margin-right: 24px;
				@include sm {
					margin-right: 0; } } }
		a {
			color: inherit;
			@include hover {
				text-decoration: underline; } }
		strong {
			@include f(14px, null, 17px);
			@include sm {
				@include f(12px, null, 14px); } } }
	&__meta {
		float: left;
		@include md {
			width: 60%; }
		li {
			&:not(:first-child) {
				@include md {
					display: none; } } } }
	&__nav {
		float: right;
		text-align: right;
		@include md {
			width: 40%; }
		li {
			@include sm {
				display: block; } } } }
