.research {
	margin: 0 auto;
	padding: 83px 20px 0;
	max-width: 1639px;
	@include sm {
		padding-top: 50px; }
	&__row {
		padding: 92px 0 100px 19.5vw;
		@include lg {
			padding-left: 0; }
		@include sm {
			padding: 40px 0; }
		&_mod {
			padding-bottom: 0;
			@include sm {
				padding-bottom: 0; } }
		&_first {
			padding-left: 400px;
			@include lg {
				padding-left: 362px; }
			@include r(960) {
				padding-left: 0; } }
		&:not(:first-child) {
			border-top: 1px solid rgba($black, .2); }
		p {
			&:not(:first-child) {
				margin-top: 48px;
				@include sm {
					margin-top: 28px; } } } }
	&__col {
		&:first-child {
			margin-top: 10px; }
		&:not(:first-child) {
			@include md {
				margin-top: 65px; } } }
	&__text {
		max-width: 800px;
		font-size: 24px;
		line-height: 1.6;
		@include lg {
			font-size: 14px; }
		@include md {
			max-width: 100%; }
		@include sm {
			font-size: 13px;
			line-height: 1.3; } }
	&__title {
		&:not(:last-child) {
			margin-bottom: 12px; } }
	&__title-medium {
		&:not(:last-child) {
			margin-bottom: 40px;
			@include sm {
				margin-bottom: 24px; } } }
	&__description {
		margin-top: 18px;
		@include f(16px, null, 1.75); }
	&__date {
		opacity: .4;
		@include f(16px, null, 1.75);
		&:not(:first-child) {
			margin-top: 21px; } }
	&__download {
		&:not(:first-child) {
			margin-top: 18px; } }
	&__doc {
		&:not(:first-child) {
			margin-top: 83px;
			@include sm {
				margin-top: 30px; } } } }

.download {
	@include f(16px, null, 1.75);
	&__link {
		font-weight: 500;
		color: $link;
		@include hover {
			span {
				border-color: $link; } }
		.icon {
			margin: 2px 8px 0 0;
			font-size: 24px;
			vertical-align: top;
			display: inline-block; }
		span {
			transition: border-color .3s;
			border-bottom: 1px solid transparent; } }
	&__size {
		margin-left: 8px;
		opacity: .6;
		display: inline; } }
.doc {
	@include clr;
	&_mb {
		margin-bottom: -30px; }
	&__img {
		float: left;
		padding-right: 10px;
		width: 217px;
		@include sm {
			width: 117px; }
		img {
			max-width: 100%;
			display: block; } }
	&__wrap {
		overflow: hidden; }
	&__title {
		max-width: 380px;
		@include sm {
			display: none; } }
	&__download {
		margin-top: 25px;
		.download__size {
			@include sm {
				padding-left: 28px;
				display: block; } } } }
