.info-box {
	padding: 81px 0 56px;
	border-top: 1px solid #D4D4D4;
	color: $dark;
	@include lg {
		padding: 40px 0; }
	@include sm {
		padding: 34px 0;
		border-top: none; }
	&__icon {
		margin-bottom: 44px;
		height: 36px;
		@include vertical;
		text-align: left;
		@include lg {
			margin-bottom: 30px; }
		@include sm {
			margin-bottom: 38px;
			height: auto; }
		.icon {
			display: inline-block;
			vertical-align: bottom;
			fill: $black;
			opacity: 0.6;
			@include sm {
				vertical-align: top; } }
		.icon-coins {
			font-size: 36px; }
		.icon-cogs {
			font-size: 34px; }
		.icon-eye {
			font-size: 28px; } }
	&__title {
		margin-bottom: 26px;
		max-width: 300px;
		@include sm {
			margin-bottom: 37px; } }
	&__description {
		@include f(18px, 400, 28px);
		@include lg {
			@include f(16px, null, 1.555); }
		@include sm {
			@include f(14, null, 21px); } } }
